#aboutme {
    overflow: hidden;
    width: 100%;
    max-width: 65rem;
     box-sizing: border-box;   
    
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    margin: 0 auto;
    /*min-width: 250px;*/
    
}
.aboutMe {
    font-size: 8rem;
    font-family: 'Bebas Neue', sans-serif;
    font-weight: 800;
    /*color: #c01033;
    min-width: 250px;*/
    color: black;
    display: flex;
        margin-top: 3rem;
        text-align: center;
        position: relative;
        z-index: 10;
        width: 100%;
        word-break: break-word;
          

    
}
.aboutPara {
    font-size: 1.5rem;
    max-width: 80%;              
   /* min-width: 250px; */      
    display: flex;
    padding: 4rem;
    margin-left: 15rem auto;
    background: #c01033;
    border-radius: 10px;
    word-break: break-word;
}

@media screen and (max-width: 600px){
    .aboutMe{
        font-size: 6rem;
    }
    .aboutPara {
    font-size: 1.2rem;
    padding: 1.5rem;
    margin: 1.5rem auto;
    max-width: 90%;
  }

}